/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import { Track } from "../../App";
import YouTube from "react-youtube";
import { MutableRefObject } from "react";

type Props = { currentTrack: Track; player: MutableRefObject<YouTube> };

const Player = ({ currentTrack, player }: Props) => {
  return (
    <YouTube
      ref={player}
      videoId={currentTrack.videoId}
      opts={{
        height: "0",
        width: "0",
        playerVars: {
          autoplay: 1,
          loop: 1,
          Playlist: currentTrack.videoId,
          mute: 1,
        },
      }}
      onReady={() => player.current.internalPlayer.playVideo()}
      onPlay={() => player.current.internalPlayer.unMute()}
      onEnd={() => player.current.resetPlayer()}
      css={css`
        display: none;
      `}
    />
  );
};

export default Player;
